body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.home__link {
  align-items: center;
  display: flex;
}

.image {
  width: 40px;
  height: 40px;
}

.links {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  margin-left: 20px;
}

.nav__link:first-child {
  margin-left: 0;
}

.text {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

